import {
    createWebHistory,
    createRouter
} from "vue-router";
import Dashboard from "@/views/Home.vue";
import Mail from "@/views/Contact.vue";
import Work from "@/views/Portfolio.vue";



const routes = [{
        path: "/",
        name: "Home",
        component: Dashboard,
    },
    {
        path: "/portfolio",
        name: "Portfolio",
        component: Work
    },
    {
        path: "/contact",
        name: "Contact",
        component: Mail
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;